<template>
    <div class="yingyong">
        <headerBar :title="headerNavData"></headerBar>
        <div class="createf">
          <p>○ {{$t('code.p1')}}</p>
          <p>○ {{$t('code.p2')}}</p>
          <p>○ {{$t('code.p5')}}</p>
        </div>
        <div class="benfen">
           <h1>{{$t('code.p6')}}</h1>
        </div>
        <div class="notice">
          <div class="skey">{{content}}</div>
          <!-- <yd-textarea slot="right"  v-model="content" placeholder="" readonly 
            v-clipboard:copy="content"
            v-clipboard:success="onCopy"
            v-clipboard:error="onError"></yd-textarea> -->
          <button type="button"  v-clipboard:copy="content"
            v-clipboard:success="onCopy"
            v-clipboard:error="onError"
            >{{$t('code.copy')}}</button>
         
        </div>
    </div>
  
</template>

<script>
  import { getPrivateFun } from '../assets/js/Keytoken.js'
  import headerBar from './header.vue'
export default {
  components:{
      "headerBar":headerBar,
  },
  data () {
    return {
      show1:false,
      headerNavData:{
         left:true,
         title:this.$t('data.Backupprivatekey'),
         right:false,
         bj:'#2c94ed',
         search:false,
         share:false
      },
      content:''
    }
  },
  created(){
    if (this.$route.query.type == 1) {
      this.headerNavData.title=this.$t('data.Backupprivatekey')
    }else{
      this.headerNavData.title=this.$t('data.Backupkeystore')
    }
    this.content = this.$route.query.key
  },
  methods:{
    showpwd(){
      this.show1 = true
    },
    gowritezjc(){
      this.show1 = false
      this.$router.push({path:'/'})
    },
    onCopy: function (e) {
      this.$dialog.toast({
          mes: this.$t('data.copycontent'),
          timeout: 2000
      });
      setTimeout(function(){
        this.gowritezjc()
      }.bind(this),2000)
      
    },
    onError: function (e) {
      this.$dialog.toast({
          mes: this.$t('data.copycontentfail'),
          timeout: 2000
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.yingyong{
  padding-top: .9rem;
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
}
.skey{
  font-size: .33rem;
  padding: .3rem;
  min-height: 2.5rem;
  height: auto;
  word-wrap:break-word;
  border:1px solid #ddd;
  border-radius: 5px;
}
.yd-textarea textarea{
  font-size: .22rem!important;
  height: 2.2rem!important
}
.benfen{
  padding: .5rem 0 .35rem;
  text-align: center;
}
.benfen h1{
  text-align: center;
  line-height: 1.5;
  font-size: .3rem;
  color: #278eed;
  margin-bottom: .2rem;
}
.benfen p{
  text-align: center;
  line-height: 1.5;
  font-size: .28rem;
  color: #333;
}
.notice{
  padding: .35rem .2rem;
}
.notice h1{
  font-weight: 100;
  font-size: .3rem;
  color: #c20919;
  margin-bottom: .3rem;
}
.notice p{
  font-size: .28rem;
  color: #333;
  line-height: 1.5;
  margin-bottom: .2rem
}
.notice button{
  background: #278eed;
  height: .7rem;
  line-height: .7rem;
  width: 100%;
  text-align: center;
  color: #fff;
  border:none;
  font-size: .3rem;
  border-radius: 5px;
  margin: .4rem 0
}
.notice button:active{
  background: #3785cd;
}
.dig{
  background: #fff;
  padding: .4rem;
  text-align: center;
}
.ttl{
  font-size: .3rem;
  color: #278eed;
}
.dig input{
  width: 100%;
  height: .7rem;
  line-height: .7rem;
  padding: 0 .12rem;
  background: #f4f4f4;
  border:none;
  border-radius: 5px;
  color: #999;
  font-size: .28rem;
  margin: .3rem 0;
}
.dig_bt button{
  margin: 0 .3rem;
  padding: 0 .5rem
}
.dig_bt button:nth-child(1){
  background: #c20919;
}
.dig_bt button:nth-child(2){
  background: #278eed;
}
.createf{
  /*background: #f4f4f4;*/
  padding: .2rem .35rem; 
  font-size: .26rem;
  color: red
}
.yd-textarea{
  border: 1px solid #ddd;
  padding: .3rem;
  color: #333;
  /*margin: .5rem 0;*/
}
</style>



